@import url(https://fonts.googleapis.com/css?family=Anton|Rock+Salt&display=swap);
.header {
  width: 100%; }
  .header .header-h1 {
    font-family: 'Rock Salt' , cursive;
    text-align: center;
    color: magenta;
    text-shadow: -2px 2px 0 #000;
    font-size: 7em; }
  .header h2 {
    text-align: center;
    color: wheat;
    text-shadow: -2px 2px 0 #000;
    font-size: 4.5em;
    font-family: 'Anton' , sans-serif;
    letter-spacing: 3px; }

* {
  margin: 0;
  padding: 0; }

#root {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100vh; }

body {
  background: url(https://images.pexels.com/photos/1011334/pexels-photo-1011334.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 100vh;
  width: 100%; }

.buttons-container {
  text-align: center; }
  .buttons-container button {
    color: #f82499;
    background-color: white;
    font-size: 2em;
    text-transform: uppercase;
    padding: 16px 48px 18px;
    border-radius: 500px;
    margin: 10px;
    cursor: pointer; }
  .buttons-container button:focus {
    outline: none; }

.submission {
  display: flex;
  justify-content: center;
  padding: 20px; }
  .submission .enter-text {
    background-color: magenta;
    color: white;
    text-shadow: -2px 2px 0 #000;
    border-radius: 80px;
    font-size: 1.3em;
    margin-right: 20px;
    padding-bottom: 3px;
    text-align: center; }
  .submission .enter-text:focus {
    outline: none; }
  .submission .enter-text::-webkit-input-placeholder {
    color: #fff;
    font-size: 1em;
    text-align: center;
    padding-bottom: 3px; }
  .submission .enter-text::-moz-placeholder {
    color: #fff;
    font-size: 1em;
    text-align: center;
    padding-bottom: 3px; }
  .submission .enter-text:-ms-input-placeholder {
    color: #fff;
    font-size: 1em;
    text-align: center;
    padding-bottom: 3px; }
  .submission .enter-text::-ms-input-placeholder {
    color: #fff;
    font-size: 1em;
    text-align: center;
    padding-bottom: 3px; }
  .submission .enter-text::placeholder {
    color: #fff;
    font-size: 1em;
    text-align: center;
    padding-bottom: 3px; }
  .submission .submit-button {
    color: #f82499;
    background-color: white;
    font-size: 1em;
    text-transform: uppercase;
    padding: 10px 30px 12px;
    border-radius: 500px; }
  .submission .submit-button:focus {
    outline: none; }

.numGuesses {
  text-align: center;
  font-size: 30px;
  color: black; }

.higherLowerMessage {
  text-align: center;
  font-size: 30px;
  color: black; }

.highScoreMessage {
  text-align: center;
  font-size: 30px;
  color: black; }

.restart-button {
  color: #f82499;
  background-color: white;
  font-size: 1em;
  text-transform: uppercase;
  padding: 16px;
  border-radius: 500px;
  margin: 10px;
  cursor: pointer;
  width: 100px;
  position: fixed;
  bottom: 0;
  right: 0; }

